<template>
  <div id="scroll">
    <!-- 月份 -->
    <div :key="contentKey">
      <div
        v-for="(item,index) in listArr"
        :key="index"
      >
        <div
          v-for="(item,i) in item.child"
          :key="i"
        >
          <div @click="toggle(item)">
            <div style="text-align:left;">
              <div style="font-weight:bold;">{{ item.y }}年{{ item.m }}月</div>
              <div
                class="money"
                v-if="priceAll[item.y+'-'+item.m]"
              >
                <div style="display:inline-block;text-align:left;">
                  总收入&ensp;
                  <span style="color:#000;font-weight:bold;">¥{{ priceAll[item.y+'-'+item.m] }}</span>
                </div>&emsp;
              </div>
            </div>
            <van-icon :name="item.statu==false?'arrow':'arrow-down'" />
          </div>
          <div>
            <ul
              v-show="item.statu"
              :key="key"
              style="margin-bottom:10px;"
            >
              <li
                class="other_box"
                style="background: #fff;line-height: 50px;color: #2c70e4;"
                @click='toShow(item)'
              >下载报表</li>
              <li
                v-for="(c_item,index) in options[item.y+'-'+item.m]"
                :key="index"
                class="other_box"
                @click="goNext(item, c_item)"
              >
                <div class="li-box">
                  <div class="img_box">
                    <img
                      class="person_img"
                      src="@/assets/head/landLord.png"
                      alt=""
                    >
                    <div>
                      <div>{{ c_item.landlord }}</div>
                      <div class="f12">{{ c_item.roomNum }}个房间</div>
                    </div>
                  </div>
                  <div class="c_00">{{ Number(c_item.balanceAmount).toFixed(2) }}
                    <van-icon name="arrow" />
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { GetBusinessLandlordBillRecordOwner, GetBusinessLandlordBillRecordExport } from '@/api/api_landlord';
import dayjs from 'dayjs';
import { draw } from '@/utils/avatar.js';
import { Dialog } from 'vant';
import axios from 'axios';
export default {
    components: {},
    data() {
        return {
            key: 0,
            listArr: [],
            item: '',
            statu: false,
            options: {},
            priceAll: {},
            contentKey: 0,
            num: '1'
        };
    },
    created() {
        let star = '2021-09';
        let end = dayjs().format('YYYY-MM');
        let num = 100;
        let timeArr = [];
        for (let i = 0; i < num; i++) {
            let timeUnix = dayjs(star).add(i, 'month').unix();
            if (timeUnix <= dayjs(end).unix()) {
                timeArr.push(dayjs(star).add(i, 'month'));
            }
        }
        this.$store.commit('setHeader', '业主结算');
        let current_time = new Date();
        let year = current_time.getFullYear();
        let mouth = current_time.getMonth() + 1 < 10 ? '0' + (current_time.getMonth() + 1) : current_time.getMonth() + 1;
        for (let i = 0; i < timeArr.length; i++) {
            let tempMouth = mouth - i;
            let tempYear = year;
            if (tempMouth <= 0) {
                let abstempMouth = Math.abs(tempMouth);
                tempMouth = 12 - (abstempMouth % 12);
                let tempYJ = parseInt(abstempMouth / 12) + 1;
                tempYear = tempYear - tempYJ;
            }
            this.listArr.push({
                y: tempYear,
                m: tempMouth < 10 ? '0' + tempMouth : tempMouth,
                statu: i == 0 ? true : false
            });
        }
        let data = {};
        this.listArr.forEach((item) => {
            if (data[item.y]) {
                data[item.y].push(item);
            } else {
                data[item.y] = [item];
            }
        });
        let arr = [];
        for (const k in data) {
            let obj = {};
            obj.year = k;
            obj.child = data[k];
            arr.push(obj);
        }
        this.listArr = arr.reverse();
        this.toggle(this.listArr[0].child[0], 1);
    },
    methods: {
        // 切换滑块
        toggle(item, first) {
            this.item = item;
            if (first == 1) {
                item.statu = true;
            } else {
                item.statu = !item.statu;
            }
            if (this.num == 2) {
                if (item.statu == true) {
                    this.getprice_long(item.y, item.m);
                }
            } else {
                if (item.statu == true) {
                    this.getprice(item.y, item.m);
                }
            }
        },
        // 日租
        getprice(y, m) {
            let data = {
                hotelId: this.$route.query.hotelId,
                billTime: this.item.y + '-' + this.item.m
            };
            GetBusinessLandlordBillRecordOwner(data).done((res) => {
                this.options[y + '-' + m] = [];
                this.options[y + '-' + m] = res.data;
                this.func(this.options[y + '-' + m], y + '-' + m);
                ++this.contentKey;
            });
        },
        func(options, ym) {
            let arr = [];
            for (const key in options) {
                arr.push(options[key]);
            }
            let priceAll = 0;
            arr.map((item, i) => {
                priceAll += item.balanceAmount;
                this.priceAll[ym] = Number(priceAll).toFixed(2);
                ++this.key;
            });
        },
        // 已出账---长租和日租---日账单
        goNext(f_item, c_item) {
            this.$router.push({
                path: '/owner-settle-finance-person',
                query: {
                    landlord: c_item.landlord,
                    landLordId: c_item.landlordId,
                    billTime: f_item.y + '-' + f_item.m
                }
            });
        },
        toShow(item) {
            Dialog.confirm({
                title: `${item.y + '年'}${item.m + '月'}下载报表?`
            })
                .then(() => {
                    let data = {
                        hotelId: this.$route.query.hotelId,
                        billTime: item.y + '-' + item.m
                    };
                    axios({
                        method: 'post',
                        url: '/api/business/landlord/bill/record/export',
                        params: data,
                        responseType: 'arraybuffer' //注意这个参数必须加，不然会出现下载好的文件打开是乱码情况
                    }).then((res) => {
                        const data = res.data;
                        const url = window.URL.createObjectURL(new Blob([data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' }));
                        const link = document.createElement('a');
                        link.style.display = 'none';
                        link.href = url;
                        link.setAttribute('download', `${item.y + '年'}${item.m + '月'}报表`);
                        //注意：上面的self.$route.query.type是你要下载的文件名加文件后缀，例如test.docx
                        document.body.appendChild(link);
                        link.click();
                        document.body.removeChild(link);
                    });
                })
                .catch(() => {});
        }
    }
};
</script>

<style scoped>
#scroll {
    padding: 15px 0;
    position: fixed;
    top: 50px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
    background-color: #f6f6f6;
    font-size: 14px;
    color: #000;
}
ul {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
.c_00 {
    color: rgb(0, 0, 0);
    display: flex;
    align-items: center;
    font-weight: bold;
}
.all {
    background: #fff;
    display: flex;
    justify-content: space-between;
    padding: 10px 25px 10px 20px;
    align-items: center;
}
.money {
    font-size: 12px;
    display: flex;
}
.li-box {
    font-size: 15px;
    display: flex;
    justify-content: space-between;
    background-color: #fff;
    padding: 12px 15px 12px 20px;
}
.other_box {
    color: #888888;
    line-height: 30px;
    border-bottom: 1px solid #f6f6f6;
}
select {
    border: solid 1px #000;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    border: none;
    outline: none;
}
select::-ms-expand {
    display: none;
}
/deep/ .van-tab {
    color: #989898;
}
/deep/ .van-tabs__line {
    width: 35%;
    background-color: #ff7d41;
}
/deep/ .van-tab--active {
    color: #ff7d41;
}
.img_box {
    display: flex;
    align-items: center;
    color: #000;
    text-align: left;
    line-height: 20px;
}
.person_img {
    width: 30px;
    height: 30px;
    border-radius: 20px;
    margin-right: 8px;
}
.f12 {
    color: #b2b2b2;
    font-size: 12px;
}
</style>



